body {
  margin: 0
    0;
}
.backgr {
  min-width: 100%;
  min-height: 100%;
  background-color: #c7b39b;
  background-image: url(./big.jpg);
  background-repeat: repeat-x;
  position: absolute;
  background-position: center
    center;
}
.card {
  width: 18%;
  display: block;
  margin: 5px;
  padding: 0;
  background-color: rgba(
    53,
    102,
    31,
    0.582
  );
  min-width: 10rem;
}
.contatiner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0
    10%;
}

.header {
  font-size: 150%;
  font-weight: 700;
  text-transform: uppercase;
  margin: 0
    10%;
  padding: 1rem;
  background-color: rgba(
    206,
    186,
    149,
    0.33
  );
  border-radius: 0.5rem;
}
.brdr {
  border-style: none;
  border-radius: 1rem;
}
.brdr:hover {
  color: black;
  background-color: rgba(
    105,
    158,
    80,
    0.5
  );
}
.brdr:active {
  background-color: rgba(
    53,
    102,
    31,
    0.582
  );
}

.carouselcont {
  margin: 0
    10%;
  border-radius: 0.5rem;
}
.placeHldr {
  width: 80%;
  border-radius: 0.5rem;
  border-style: double;
  margin: 1rem
    auto;
  padding: 8rem
    0;
  text-align: center;
  font-style: normal;
}

@media (max-width: 1000px) {
  .backgr {
    background-color: #c7b39b;
    background-image: url(./smol.jpg);
  }
  .placeHldr {
    display: none;
  }
  .card {
    width: 31%;
  }
  .contatiner {
    justify-content: center;
  }
}
